/* VRH Buildcon Custom Styles */

.App {
    min-height: 100vh;
}

/* Hero Section */
.hero-gradient {
    background: linear-gradient(to bottom, rgba(5, 5, 5, 0) 0%, rgba(5, 5, 5, 0.7) 50%, #050505 100%);
}

/* Service Card Gradient */
.service-card-gradient {
    background: linear-gradient(180deg, transparent 0%, rgba(5, 5, 5, 0.95) 100%);
}

/* Gold border animation */
.gold-border-animate {
    position: relative;
}

.gold-border-animate::after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 0;
    height: 2px;
    background: #D4AF37;
    transition: width 0.3s ease;
}

.gold-border-animate:hover::after {
    width: 100%;
}

/* Section transitions */
.section-padding {
    padding: 6rem 1rem;
}

@media (min-width: 768px) {
    .section-padding {
        padding: 8rem 2rem;
    }
}

@media (min-width: 1024px) {
    .section-padding {
        padding: 10rem 2rem;
    }
}

/* Bento Grid */
.bento-grid {
    display: grid;
    gap: 1rem;
}

@media (min-width: 768px) {
    .bento-grid {
        grid-template-columns: repeat(12, 1fr);
        gap: 1.5rem;
    }
}

/* Counter animation */
@keyframes countUp {
    from {
        opacity: 0;
        transform: translateY(20px);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}

.counter-animate {
    animation: countUp 0.8s ease-out forwards;
}

/* Form focus styles */
.form-input:focus {
    outline: none;
    border-color: #D4AF37;
    box-shadow: 0 0 0 2px rgba(212, 175, 55, 0.1);
}

/* Project card hover */
.project-card {
    position: relative;
    overflow: hidden;
}

.project-card::before {
    content: '';
    position: absolute;
    inset: 0;
    background: linear-gradient(to top, rgba(0, 0, 0, 0.9) 0%, transparent 60%);
    z-index: 1;
    opacity: 0.7;
    transition: opacity 0.3s ease;
}

.project-card:hover::before {
    opacity: 1;
}

/* Testimonial card */
.testimonial-card {
    background: linear-gradient(135deg, #121212 0%, #0a0a0a 100%);
}

/* Navigation active state */
.nav-link-active {
    color: #D4AF37 !important;
}

/* Responsive text */
.text-responsive-hero {
    font-size: clamp(2.5rem, 8vw, 6rem);
}

.text-responsive-section {
    font-size: clamp(2rem, 5vw, 3.5rem);
}

/* Loading spinner */
.spinner {
    border: 2px solid #27272A;
    border-top: 2px solid #D4AF37;
    border-radius: 50%;
    width: 24px;
    height: 24px;
    animation: spin 0.8s linear infinite;
}

@keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
}
